import * as React from "react";
import Layout from "../components/layout";
import Banner from "../screens/main/banner";
// import Popular from "../screens/main/popular";
import AboutUs from "../screens/main/about";
import OurService from "../screens/main/our-service";
import OurDevelopment from "../screens/main/our-development";
import RecentWorks from "../screens/main/recent-works";
import OurTeam from "../screens/main/our-team";
import MoveForward from "../components/common/move-forward";
import Seo from "../components/seo";
import { graphql } from "gatsby";
import { initializeApp } from "firebase/app";
import { getFirestore } from "firebase/firestore/lite";
import CtaButton from "../components/common/contactbutton";
import Faq from "../components/common/faq";
import Achievement from "../screens/main/achievement";

const IndexPage = (props) => {
  const app = initializeApp(props?.data?.site?.siteMetadata?.firestore);
  const db = getFirestore(app);

  const faqData = [
    {
      question: "What services does Justtry Technologies offer?",
      answer:
        "Blockchain, DeFi, Wallet, Web2, Web3, Exchange, NFT development.",
    },
    {
      question: "What makes Justtry Technologies stand out?",
      answer:
        "Leading software development company specializing in cutting-edge technologies.",
    },
    {
      question: "Can Justtry Technologies handle diverse projects?",
      answer:
        "Yes, we excel in blockchain, DeFi, wallets, web development, and NFTs.",
    },
    {
      question: "Why choose Justtry Technologies for software development?",
      answer:
        "Expertise in building innovative solutions for blockchain and decentralized technologies.",
    },
  ];

  return (
    <Layout db={db}>
      <Banner db={db} />
      {/* <Popular /> */}
      <AboutUs db={db} />
      <Achievement db={db} />
      <OurService db={db} />
      <OurDevelopment />
      <CtaButton db={db} />
      <RecentWorks />
      <OurTeam />
      <Faq faqData={faqData} />
      <MoveForward db={db} />
    </Layout>
  );
};

export const Head = () => (
  <Seo
    title="Blockchain Development Company | Justtry Technologies"
    headTitle="Blockchain Development Company | Blockchain Development Services"
    description="Justtry Technologies, a trailblazing software development company, pioneers cutting-edge solutions for diverse industries. With a focus on innovation and client satisfaction, we deliver bespoke software that transforms businesses. Our expertise spans web and mobile applications, AI, and more, ensuring seamless integration of technology to drive success."
    keywords="blockchain development company, blockchain development services, software development services, software development, web development services, mean stack development, mern stack development, mobile app development, full stack development."
  ></Seo>
);

export default IndexPage;

export const queryData = graphql`
  query MyQuery {
    site {
      siteMetadata {
        firestore {
          apiKey
          appId
          authDomain
          messagingSenderId
          projectId
          storageBucket
        }
      }
    }
  }
`;
