import React from "react";
import { Container, Row, Col } from "react-bootstrap";
import sec from "../../styles/assets/images/recentworks/focus_group.webp";
import TalkToUsNew from "../../components/common/talk-to-us-new";
import { Link } from "gatsby";

const AboutUs = (props) => {
  const { db } = props;
  return (
    <Container>
      <Row style={{ marginBottom: "50px", marginTop: "50px", rowGap: "30px" }}>
        <Col
          xxl={6}
          xl={6}
          lg={6}
          md={12}
          className="d-flex align-items-center justify-content-center"
        >
          <div className="about_us_sec_lhs">
            <h2>
              Our Professionals <span className="about_us_clr_text">Focus</span>
            </h2>
            <div className="img_sec">
              <img
                className="focus_img"
                src={sec}
                alt="about justtry technolgies"
              />
            </div>
          </div>
        </Col>
        <Col
          xxl={6}
          xl={6}
          lg={6}
          md={12}
          className="d-flex align-items-center justify-content-center"
        >
          <div className="about_us_sec_rhs">
            <div className="about_rhs_content_top">
              <p>
                Justtry is your trusted partner in software development,
                offering tailored solutions for startups and enterprises. With a
                dynamic team of over 10+ skilled developers, we specialize in
                crafting MVP software for blockchain ventures.{" "}
              </p>
              <p>
                Our expertise spans a wide range of technologies, including
                Node.js, Angular.js, React.js, Laravel, Mongo DB, and MySQL.
                We've been delivering top-notch IT services globally for more
                than 3+ years.
              </p>
              <p>
                {" "}
                At Justtry, we provide flexible hiring models, including fixed
                projects, hourly projects, dedicated resource hiring, and
                offshore development centers. We're committed to driving your
                business growth through innovative technology solutions.
              </p>
            </div>
            <div className="decenter_hero_sec_btn">
              <Link to="/contact-us" className="about_rhs_btn">
                View Live Demo
              </Link>
              <TalkToUsNew label="Free Consult" isFor="about" db={db} />
            </div>
          </div>
        </Col>
      </Row>
    </Container>
  );
};

export default AboutUs;
