import React, { useState, useEffect, useRef } from "react";
import { Container, Row, Col } from "react-bootstrap";

const Achievement = () => {
  const Counter = ({ targetNumber, isHovered }) => {
    const [count, setCount] = useState(0);
    const intervalRef = useRef(null);

    useEffect(() => {
      const updateCounter = () => {
        setCount((prevCount) => prevCount + 1);
      };

      if (isHovered) {
        intervalRef.current = setInterval(updateCounter, 10);
      } else {
        clearInterval(intervalRef.current);
      }

      return () => clearInterval(intervalRef.current);
    }, [isHovered]);

    return <span className="num_count">{count <= targetNumber ? count : targetNumber}</span>;
  };

  const [isRowHovered, setIsRowHovered] = useState(false);

  const handleRowMouseOver = () => {
    setIsRowHovered(true);
  };

  const achievementData = [
    { targetNumber: 25, label: "Happy Clients" },
    { targetNumber: 55, label: "Success Projects" },
    { targetNumber: 15, label: "Our Experts" },
    { targetNumber: 10, label: "Years of Experience" },
  ];

  return (
    <Container>
      <Row className="achievement_sec_row" onMouseOver={handleRowMouseOver}>
        {achievementData.map((achievement, index) => (
          <Col
            xl={3}
            lg={6}
            md={6}
            sm={12}
            className="d-flex justify-content-center"
            key={index}
          >
            <div className="achievement_btn_type">
              <Counter
                targetNumber={achievement.targetNumber}
                isHovered={isRowHovered}
              />
              <p>{achievement.label}</p>
            </div>
          </Col>
        ))}
      </Row>
    </Container>
  );
};

export default Achievement;
